.dm_topMenu {
  width: 100%;
  height: 48px;
  line-height: 48px;
  padding: 10px 0;
  background: #555;
}
.dm_topMenu .ant-row {
  height: 100%;
  display: flex;
  align-items: center;
  color: #fff;
}
.dm_topMenu .ant-row .ant-col-6 {
  cursor: pointer;
}
.dm_topMenu .ant-row .ant-col-6:hover {
  text-decoration: underline;
}
.dm_topMenu .ant-row .ant-col-18 {
  display: flex;
  justify-content: flex-end;
}
.dm_topMenu .ant-row .ant-col-18 span {
  cursor: pointer;
}
.dm_topMenu .ant-row .ant-col-18 span:not(:last-child):after {
  content: '|';
  padding: 0 10px;
}
.dm_topMenu .ant-row .ant-col-18 span:hover {
  text-decoration: underline;
}
