.dm_Products {
  padding-bottom: 30px;
}
.dm_Products .ant-row .ant-col-6 {
  width: calc(25% - 15px);
  margin: 0 20px 20px 0;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.dm_Products .ant-row .ant-col-6:nth-child(4n) {
  margin-right: 0;
}
.dm_Products .ant-row .ant-col-6 .ant-card .ant-card-cover {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dm_Products .ant-row .ant-col-6 .ant-card .ant-card-cover > img {
  width: 74%;
}
.dm_Products .ant-row .ant-col-6 .ant-card .ant-card-body {
  padding: 0 24px 24px;
}
.dm_Products .ant-row .ant-col-6 .ant-card .ant-card-body .ant-card-meta-title {
  margin-bottom: 0;
}
.dm_Products .ant-row .ant-col-6 .ant-card .ant-card-body .ant-card-meta-title .ant-typography {
  margin-bottom: 0;
  color: #1890ff;
}
.dm_Products .ant-row .ant-col-6 .ant-card .ant-card-body .ant-card-meta-description > a {
  color: #808080;
  font-size: 12px;
}
.dm_Products .ant-row .ant-col-6 .ant-card .ant-card-body .ant-card-meta-description:hover > a {
  color: #1890ff;
  text-decoration: underline;
}
.dm_Products .ant-pagination {
  text-align: right;
  padding: 10px 0;
}
