.dm_FooterCopyright {
  width: 100%;
  background: #e0e0e0;
}
.dm_FooterCopyright > .ant-row {
  padding: 6% 0;
}
.dm_FooterCopyright > .ant-row .ant-col-8 {
  padding-left: 80px;
}
.dm_FooterCopyright > .ant-row .ant-col-8 i,
.dm_FooterCopyright > .ant-row .ant-col-8 a {
  display: block;
}
.dm_FooterCopyright > .ant-row .ant-col-8 i {
  font-size: 18px;
  font-style: normal;
  padding-bottom: 10px;
  color: #212529;
  font-weight: bold;
}
.dm_FooterCopyright > .ant-row .ant-col-8 a {
  color: #555;
  line-height: 2rem;
  font-size: 14px;
}
.dm_FooterCopyright > .ant-row .ant-col-8 a:hover {
  text-decoration: underline;
}
.dm_FooterCopyright .copyright {
  height: 58px;
  line-height: 58px;
  background: #555;
  color: #fff;
  text-align: center;
}
