.dm_CarouselBox {
  border: solid 1px #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.dm_CarouselBox .ant-carousel .slick-list .slick-slide img {
  width: 100%;
}
.dm_CarouselBox .ant-carousel .slick-dots {
  height: 28px;
  display: flex !important;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
  padding-right: 20px;
}
.dm_CarouselBox .ant-carousel .slick-dots > li {
  padding: 6px;
}
.dm_CarouselBox .ant-carousel .slick-dots > li button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #D6D6D6;
  opacity: 1;
}
.dm_CarouselBox .ant-carousel .slick-dots > li button:hover {
  background: #869791;
}
.dm_CarouselBox .ant-carousel .slick-dots > li button:before {
  content: '' !important;
}
.dm_CarouselBox .ant-carousel .slick-dots > li.slick-active button {
  background: #1890ff;
}
