.dm_MyOrder {
  height: 100%;
  background: #fff;
  margin-bottom: 30px !important;
  border: solid 1px #e6e6e6;
  padding: 20px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.dm_MyOrder .table_title h4 {
  margin-bottom: 0;
}
.dm_MyOrder .table_title div {
  display: flex;
  padding: 10px 0;
}
.dm_MyOrder .table_title div i {
  font-style: normal;
  font-weight: bold;
  padding: 0 4px;
}
.dm_MyOrder .operation {
  display: flex;
  justify-content: center;
}
.dm_MyOrder .operation a {
  padding: 0 6px;
}
.dm_MyOrder .operation a:hover {
  color: #40A9FF;
  text-decoration: underline;
}
.dm_MyOrder .imgs-style {
  width: 100%;
  cursor: pointer;
}
.dm_MyOrder .title-style > p {
  margin: 0;
  cursor: pointer;
}
.dm_MyOrder .title-style > p:hover {
  color: #40A9FF;
  text-decoration: underline;
}
.dm_MyOrder .title-style > span {
  font-size: 12px;
  color: #999;
}
.dm_MyOrder .ant-table-footer {
  height: 55px;
  line-height: 55px;
  padding: 0;
}
.dm_MyOrder .ant-table-footer .ant-row .left button {
  margin: 0 16px;
}
.dm_MyOrder .ant-table-footer .ant-row .right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.dm_MyOrder .ant-table-footer .ant-row .right .num {
  display: flex;
  padding-right: 20px;
}
.dm_MyOrder .ant-table-footer .ant-row .right .num i {
  color: #40A9FF;
  font-weight: bold;
  padding: 0 3px;
  font-style: normal;
  font-size: 14px;
}
.dm_MyOrder .ant-table-footer .ant-row .right > div {
  display: flex;
  padding-right: 20px;
}
.dm_MyOrder .ant-table-footer .ant-row .right > div span {
  color: #40A9FF;
  font-size: 18px;
  font-weight: bold;
}
.dm_MyOrder .ant-table-footer .ant-row .right .go-pay {
  width: 94px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  background: #40A9FF;
  color: #fff;
}
