.dm_recommend {
  display: flex;
  margin: 30px 0;
}
.dm_recommend .ant-col-8 {
  width: 32%;
  padding: 20px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
  border: solid 1px #e6e6e6;
  background-position: center;
  position: relative;
  background: #fff;
  cursor: pointer;
}
.dm_recommend .ant-col-8:not(:last-child) {
  margin-right: 2%;
}
.dm_recommend .ant-col-8 > img {
  height: 120px;
  position: absolute;
  left: 0;
  top: 50%;
  opacity: 0.2;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.dm_recommend .ant-col-8 > div {
  text-align: right;
}
.dm_recommend .ant-col-8 > div > div {
  font-size: 20px;
  color: #1890ff;
}
.dm_recommend .ant-col-8 > div p {
  margin: 0;
  padding: 10px 0;
}
