.dm_MyEvaluation {
  height: 100%;
  background: #fff;
  margin-bottom: 30px !important;
  border: solid 1px #e6e6e6;
  padding: 20px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.dm_MyEvaluation .table_title h4 {
  margin-bottom: 0;
}
.dm_MyEvaluation .table_title div {
  width: 100%;
  height: 1px;
  background: #f0f0f0;
  margin: 6px 0;
}
.dm_MyEvaluation .product_params .ant-col-24 {
  display: flex;
  padding: 6px 0;
}
.dm_MyEvaluation .product_params .ant-col-24 > div {
  font-weight: bold;
  font-size: 14px;
}
.dm_MyEvaluation .evaluation_textArea .ant-col-24 .ant-form label {
  font-size: inherit;
  color: inherit;
  font-weight: bold;
  font-size: 14px;
}
.dm_MyEvaluation .evaluation_textArea .ant-col-24 .ant-form-item-control-wrapper textarea {
  font-size: inherit;
}
.dm_MyEvaluation .evaluation_submit {
  text-align: center;
}
.dm_MyEvaluation .evaluation_submit button {
  width: 220px;
}
