.dm_UserCenter {
  height: 100%;
  background: #fff;
  margin-bottom: 30px !important;
  border: solid 1px #e6e6e6;
  padding: 20px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.dm_UserCenter .dm_PersonalInformation .ant-row .ant-col-12,
.dm_UserCenter .dm_LoginPassword .ant-row .ant-col-12 {
  padding: 4px 0;
}
.dm_UserCenter .dm_PersonalInformation .ant-row .ant-col-12 .ant-form-item,
.dm_UserCenter .dm_LoginPassword .ant-row .ant-col-12 .ant-form-item {
  display: flex;
}
.dm_UserCenter .dm_PersonalInformation .ant-row .ant-col-12 .ant-form-item.ant-form-item-with-help,
.dm_UserCenter .dm_LoginPassword .ant-row .ant-col-12 .ant-form-item.ant-form-item-with-help {
  margin-bottom: 0;
}
.dm_UserCenter .dm_PersonalInformation .ant-row .ant-col-12 .ant-form-item .ant-form-item-label,
.dm_UserCenter .dm_LoginPassword .ant-row .ant-col-12 .ant-form-item .ant-form-item-label {
  width: 90px;
}
.dm_UserCenter .dm_PersonalInformation .ant-row .ant-col-12 .ant-form-item .ant-form-item-label label,
.dm_UserCenter .dm_LoginPassword .ant-row .ant-col-12 .ant-form-item .ant-form-item-label label {
  font-size: 12px;
}
.dm_UserCenter .dm_PersonalInformation .ant-row .ant-col-12 .ant-form-item .ant-form-item-control-wrapper,
.dm_UserCenter .dm_LoginPassword .ant-row .ant-col-12 .ant-form-item .ant-form-item-control-wrapper {
  flex: 1 1;
}
.dm_UserCenter .dm_PersonalInformation .ant-row .ant-col-12 .ant-form-item .ant-form-item-control-wrapper .ant-form-item-children,
.dm_UserCenter .dm_LoginPassword .ant-row .ant-col-12 .ant-form-item .ant-form-item-control-wrapper .ant-form-item-children {
  display: block;
}
.dm_UserCenter .dm_PersonalInformation .ant-row .ant-col-12 .ant-form-item .ant-form-item-control-wrapper .ant-form-item-children .ant-input,
.dm_UserCenter .dm_LoginPassword .ant-row .ant-col-12 .ant-form-item .ant-form-item-control-wrapper .ant-form-item-children .ant-input {
  font-size: 12px;
}
.dm_UserCenter .dm_PersonalInformation .ant-row .ant-col-12 .ant-form-item .ant-form-item-control-wrapper .ant-form-item-children .ant-radio-group.ant-radio-group-outline,
.dm_UserCenter .dm_LoginPassword .ant-row .ant-col-12 .ant-form-item .ant-form-item-control-wrapper .ant-form-item-children .ant-radio-group.ant-radio-group-outline {
  margin-top: -5px;
}
.dm_UserCenter .dm_PersonalInformation .ant-row.submit .ant-col-24,
.dm_UserCenter .dm_LoginPassword .ant-row.submit .ant-col-24 {
  text-align: center;
  padding: 10px 0;
}
.dm_UserCenter .dm_PersonalInformation .ant-row.submit .ant-col-24 .ant-btn,
.dm_UserCenter .dm_LoginPassword .ant-row.submit .ant-col-24 .ant-btn {
  width: 104px;
}
.dm_UserCenter .operation_btn a {
  padding: 0 4px;
}
.dm_UserCenter .operation_btn a:hover {
  text-decoration: underline;
}
.dm_UserCenter .table_title h4 {
  margin-bottom: 0;
}
.dm_UserCenter .table_title div {
  display: flex;
  padding: 10px 0;
}
.dm_UserCenter .table_title div i {
  font-style: normal;
  font-weight: bold;
  padding: 0 4px;
}
