.dm_HotThisWeek .title {
  width: 100%;
  height: 92px;
  line-height: 92px;
  background: #fff;
  text-align: center;
  font-size: 26px;
  color: #1890ff;
  border: solid 1px #e6e6e6;
  border-left: 0 none;
  border-right: 0 none;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.dm_HotThisWeek .hot_content {
  margin: 30px 0;
}
.dm_HotThisWeek .hot_content .slick-slider {
  height: 338px;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-arrow {
  width: 50px;
  height: 50px;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-arrow::before {
  font-size: 50px;
  color: #d6d6d6;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-arrow.slick-prev {
  left: -50px;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-arrow.slick-next {
  right: -50px;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list {
  width: 100%;
  height: 100%;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track {
  width: 100% !important;
  height: 100%;
  display: flex;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide {
  width: 200px !important;
  margin: 0 10px;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide > div > .ant-card {
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
  border: solid 1px #e6e6e6;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide > div > .ant-card .ant-card-cover {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 180px;
  max-height: 180px;
  cursor: move;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide > div > .ant-card .ant-card-cover > img {
  opacity: 0.8;
  width: 90%;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide > div > .ant-card .ant-card-body {
  padding: 20px 24px;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide > div > .ant-card .ant-card-body .ant-card-meta-title > span {
  font-weight: bold;
  color: #555;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 16px;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide > div > .ant-card .ant-card-body .ant-card-meta-title:hover span {
  text-decoration: underline;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide > div > .ant-card .ant-card-body .ant-card-meta-description span {
  display: block;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide > div > .ant-card .ant-card-body .ant-card-meta-description span.detials {
  height: 54px;
  max-height: 54px;
  overflow: hidden;
  text-align: justify;
}
.dm_HotThisWeek .hot_content .slick-slider .slick-list .slick-track .slick-slide > div > .ant-card .ant-card-body .ant-card-meta-description span.price {
  font-size: 18px;
  color: #1890ff;
  padding: 4px 0;
  font-weight: bold;
}
