.dm_SearchResults {
  height: 100%;
  background: #fff;
  margin-bottom: 30px !important;
  border: solid 1px #e6e6e6;
  padding: 20px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.dm_SearchResults .table_title {
  border-bottom: 1px solid #E8E8E8;
}
.dm_SearchResults .table_title h4 {
  margin-bottom: 0;
}
.dm_SearchResults .table_title div {
  display: flex;
  padding: 10px 0;
}
.dm_SearchResults .table_title div i {
  font-style: normal;
  font-weight: bold;
  padding: 0 4px;
}
.dm_SearchResults .imgs-style {
  width: 100%;
  cursor: pointer;
}
