.dm_ProductsDetail {
  margin-bottom: 30px;
}
.dm_ProductsDetail .CommoditySpecification {
  width: 100%;
  height: 100%;
  background: #fff;
  margin-bottom: 20px;
  border: solid 1px #e6e6e6;
  padding: 20px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-8 dl dt {
  cursor: pointer;
  text-align: center;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-8 dl dt img {
  width: 90%;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-8 dl dd {
  display: flex;
  width: 100%;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-8 dl dd > div {
  width: 20%;
  border: 1px solid #fff;
  cursor: pointer;
  background: #fff;
  padding: 6px;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-8 dl dd > div img {
  width: 100%;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-8 dl dd > div:hover {
  border-color: #e8e8e8;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-8 dl dd > div.active {
  border-color: #e8e8e8;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 {
  padding: 12px 20px 0;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 > .ant-typography {
  margin-bottom: 0;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 > h3 {
  padding: 6px 0;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .price {
  display: flex;
  align-items: center;
  padding: 10px 0;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .price > h3 {
  display: inline-block;
  color: #1890ff;
  margin-bottom: 0;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .Number {
  display: flex;
  align-items: center;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .Specifications .ant-col-2 {
  padding: 6px 0;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .Specifications .ant-col-22 .ant-col-11 {
  border: 1px solid #dadada;
  cursor: pointer;
  margin-bottom: 10px;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .Specifications .ant-col-22 .ant-col-11 .ant-typography {
  font-size: 12px;
  margin-bottom: 0;
  padding: 10px;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .Specifications .ant-col-22 .ant-col-11:hover {
  border-color: #1890ff;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .Specifications .ant-col-22 .ant-col-11:hover .ant-typography {
  color: #1890ff;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .Specifications .ant-col-22 .ant-col-11.active {
  border-color: #1890ff;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .Specifications .ant-col-22 .ant-col-11.active .ant-typography {
  color: #1890ff;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .handleButton {
  padding: 28px 0;
}
.dm_ProductsDetail .CommoditySpecification > .ant-row .ant-col-16 .handleButton .ant-btn {
  margin-right: 10px;
}
.dm_ProductsDetail .CommodityDetails {
  width: 100%;
  height: 100%;
  background: #fff;
  border: solid 1px #e6e6e6;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.dm_ProductsDetail .CommodityDetails .ant-card-head .ant-tabs-tab {
  font-size: 14px;
  font-weight: bold;
}
.dm_ProductsDetail .CommodityDetails .ant-card-body {
  padding: 20px   24px;
}
.dm_ProductsDetail .CommodityDetails .ant-card-body .Pictures .ant-descriptions-view .ant-descriptions-row {
  display: flex;
  flex-direction: column;
}
.dm_ProductsDetail .CommodityDetails .ant-card-body .Parameter .ant-descriptions .ant-descriptions-title,
.dm_ProductsDetail .CommodityDetails .ant-card-body .Pictures .ant-descriptions .ant-descriptions-title {
  font-size: 12px;
  margin-bottom: 10px;
}
.dm_ProductsDetail .CommodityDetails .ant-card-body .Parameter .ant-descriptions .ant-descriptions-view,
.dm_ProductsDetail .CommodityDetails .ant-card-body .Pictures .ant-descriptions .ant-descriptions-view {
  text-align: center;
}
.dm_ProductsDetail .CommodityDetails .ant-card-body .Parameter .ant-descriptions .ant-descriptions-view td,
.dm_ProductsDetail .CommodityDetails .ant-card-body .Pictures .ant-descriptions .ant-descriptions-view td {
  padding-bottom: 10px;
}
.dm_ProductsDetail .CommodityDetails .ant-card-body .Parameter .ant-descriptions .ant-descriptions-view td > span,
.dm_ProductsDetail .CommodityDetails .ant-card-body .Pictures .ant-descriptions .ant-descriptions-view td > span {
  font-size: 12px;
}
