.dm_OrderDetails {
  height: 100%;
  background: #fff;
  margin-bottom: 30px !important;
  border: solid 1px #e6e6e6;
  padding: 20px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.dm_OrderDetails .table_title h4 {
  margin-bottom: 0;
}
.dm_OrderDetails .table_title div {
  display: flex;
  padding: 10px 0;
}
.dm_OrderDetails .table_title div i {
  font-style: normal;
  font-weight: bold;
  padding: 0 4px;
}
.dm_OrderDetails .imgs-style {
  width: 100%;
  cursor: pointer;
}
.dm_OrderDetails .title-style > p {
  margin: 0;
  cursor: pointer;
}
.dm_OrderDetails .title-style > p:hover {
  color: #40A9FF;
  text-decoration: underline;
}
.dm_OrderDetails .title-style > span {
  font-size: 12px;
  color: #999;
}
.dm_OrderDetails .orider_details .ant-col-12 {
  padding: 10px 30px;
}
.dm_OrderDetails .orider_details .ant-col-12 dl dt {
  font-size: 14px;
  padding: 10px 0;
  font-weight: bold;
}
.dm_OrderDetails .orider_details .ant-col-12 dl dd {
  display: flex;
  justify-content: flex-start;
  line-height: 24px;
}
.dm_OrderDetails .orider_details .ant-col-12 dl dd div {
  width: 72px;
}
.dm_OrderDetails .orider_details .ant-col-12 dl dd p {
  flex: 1 1;
}
