.dm_SearchArea,
.dm_SearchInput {
  width: 100%;
  background: #fff;
}
.dm_SearchArea {
  height: 82px;
  line-height: 82px;
  border-bottom: 1px solid #e6e6e6;
}
.dm_SearchArea .ant-row {
  display: flex;
  align-items: center;
}
.dm_SearchArea .ant-row .ant-col-16 {
  display: flex;
  padding-left: 30px;
}
.dm_SearchArea .ant-row .ant-col-16 > a {
  display: block;
  min-width: 90px;
  text-align: center;
  height: 100%;
  padding: 0 15px;
  font-size: 14px;
  color: #555;
  font-weight: bold;
}
.dm_SearchArea .ant-row .ant-col-16 > a:hover {
  color: #fff;
  background-color: #1890ff;
}
.dm_SearchArea .ant-row .ant-col-16 > a.active {
  color: #fff;
  background-color: #1890ff;
}
.dm_SearchArea .ant-row .ant-col-4 {
  display: flex;
  justify-content: space-between;
}
.dm_SearchArea .ant-row .ant-col-4.logo {
  cursor: pointer;
}
.dm_SearchArea .ant-row .ant-col-4 button i {
  font-size: 15px;
}
.dm_SearchArea .ant-row .ant-col-4 .search {
  width: 46px;
}
.dm_SearchArea .ant-row .ant-col-4 .cart {
  width: 120px;
  display: flex;
  align-items: center;
}
.dm_SearchArea .ant-row .ant-col-4 .cart span {
  margin-left: 4px;
}
.dm_SearchArea .ant-row .ant-col-4 .ant-badge-count {
  line-height: 19px;
}
.dm_SearchInput {
  height: 74px;
}
.dm_SearchInput .ant-row {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.dm_SearchInput .ant-row .ant-input-search {
  max-width: 500px;
}
.dm_SearchInput .ant-row .ant-input-search button i {
  font-size: 15px;
}
