.dm_Register {
  background: #fff;
  position: relative;
}
.dm_Register .content {
  width: 100%;
  height: 100vh;
}
.dm_Register .content .ant-form {
  max-width: 450px;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  padding: 42px 24px 50px;
  position: fixed;
  right: 0;
  top: 0;
}
.dm_Register .content .ant-form .ant-col-24 .ant-row.ant-form-item {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 24px;
}
.dm_Register .content .ant-form .ant-col-24 .ant-row.ant-form-item .ant-form-item-label > label {
  color: #fff;
}
.dm_Register .content .ant-form .ant-col-24 .ant-row.ant-form-item .ant-form-item-control-wrapper .ant-form-item-control {
  width: 300px;
  text-align: center;
}
.dm_Register .content .ant-form .ant-col-24 .ant-row.ant-form-item .ant-form-item-control-wrapper .ant-form-item-control.has-error {
  text-align: left;
}
.dm_Register .logo a {
  position: absolute;
  left: 0;
  top: 0;
  padding: 42px 24px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
  border: 1px solid #ffffff1a;
  border-left: 0 none;
  border-top: 0 none;
}
